// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios' // axios请求插件
import path from './components/_path.js' // http请求
import './assets/scss/font-awesome.css' // 阿里图标库
import './assets/scss/reset.css' // 全局样式

Vue.config.productionTip = false
/* eslint-disable no-new */
Vue.prototype.path = path
Vue.prototype.$http = axios
Vue.filter('date', function (input) { // 日期
  var d = new Date(input)
  var year = d.getFullYear()
  var month = d.getMonth() + 1
  var day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate()
  return year + '-' + month + '-' + day
})
Vue.filter('rateOfInterest', function (input) { // 起息日期
  switch (input) {
    case 0:
      return ('T+0')
    case 1:
      return ('T+1')
    case 2:
      return ('T+2')
  }
})
Vue.filter('refundStyle', function (input) { // 还款方式
  switch (input) {
    case 0:
      return ('按月付息到期还本')
    case 1:
      return ('本息一次还')
  }
})
new Vue({
  el: '#app',
  router,
  axios,
  components: { App },
  template: '<App/>'
})



