import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import mainView from '@/components/mainView'
import my from '@/components/my/my'
import recommend from '@/components/recommend/recommend'
import money from '@/components/money/money'
import productDetail from '@/components/money/productDetail'
import compute from '@/components/money/compute'
import more from '@/components/money/more'
import sonView from '@/components/sonView'
import myMessage from '@/components/my/myMessage'
import mySetting from '@/components/my/mySetting'
import myHelp from '@/components/my/myHelp'
import grayHead from '@/components/grayHead'
import myOur from '@/components/my/myOur'
import myIeda from '@/components/my/myIeda'
import myUse from '@/components/my/myUse'
import myEmail from '@/components/my/myEmail'
// import myUse from '@/components/my/myUse'
Vue.use(Router)

export default new Router({
  routes: [
    { // 软件加载页面+第一次使用的引导页面，如果不是第一次打开就跳往推荐页面
      path: '/',
      name: 'login',
      component: login
    },
    { // 头尾的模板指令（每个页面设定头尾的v-show,变量为true,或者false，来决定头尾模板的显示
      path: '/main',
      name: 'main',
      component: mainView,
      children: [
        { // 推荐页面默认首页
          path: '/recommend',
          name: 'main.recommend',
          component: recommend
        },
        { // 理财页面
          path: 'money',
          name: 'main.money',
          component: money
        },
        { // 投资页面
          path: 'invest',
          name: 'main.invest',
          component: login
        },
        { // 我的页面
          path: 'my',
          name: 'main.my',
          component: my
        }
      ]
    },
    { // 只有头部的样式
      path: '/a',
      name: 'son',
      component: sonView,
      children: [
        { // 消息中心
          path: '/my/myMessage',
          name: 'myMessage',
          component: myMessage
        },
        { // 跟多设置
          path: '/my/mySetting',
          name: 'mySetting',
          component: mySetting
        },
        { // 我的账户设置
          path: '/my/myUse',
          name: 'myUse',
          component: myUse
        },
        {
          path: '/my/:id',
          component: myEmail
        },
        { // 产品计算页面
          path: '/money/compute',
          name: 'compute',
          component: compute
        },
        {// 产品更多页面
          path: '/money/more',
          name: 'more',
          component: more
        }
      ]
    },
    { // 头部是灰色的样式
      path: '/a',
      name: 'gray',
      component: grayHead,
      children: [
        {
          path: '/my/myHelp',
          name: 'myHelp',
          component: myHelp
        },
        {
          path: '/my/myOur',
          name: 'myOur',
          component: myOur
        },
        {
          path: '/my/myIeda',
          name: 'myIeda',
          component: myIeda
        }
      ]
    },
    { // 交易流水页面
      path: '/my/water',
      name: 'myWater',
      component: sonView
    },
    { // 产品详情
      path: '/money/productDetail',
      name: 'productDetail',
      component: productDetail
    }
  ]
})



