export default{
  wh: { // 我的页面请求
    method: 'get',
    url: '/a/u/user/123'
  },
  myMessage: function (id) { // 消息中心
    return {
      method: 'get',
      url: '/a/u/user/userMessage/list/' + id
    }
  },
  myHelp: { // 帮助中心
    method: 'get',
    url: '/a/u/user/help',
    parms: {
      dataName: '1'
    }
  },
  myOur: { // 关于我们
    method: 'get',
    url: '/a/u/user/about',
    params: {
      dataName: '1'
    }
  },
  myIeda: function (data) { // 意见反馈
    return {
      method: 'post',
      url: '/a/u/user/feedback',
      params: data
    }
  },
  myUpdata: { // 系统升级
    method: 'get',
    url: '/a/u/user/Update'
  },
  myUse: {
    method: 'get',
    url: 'a/u/user/set/123'
  },
  product: { // 产品列表
    method: 'get',
    url: '/a/product/list'
  },
  // 续投列表
  continue: {
    method: 'get',
    url: '/a/u/transaction/list/continue'
  },
  // 产品详情
  productDetail: function (id) {
    return {
      method: 'get',
      url: '/a/u/product/' + id
    }
  }
}



