<template>
  <div class="money-view btd9 tabBox" id="leftTabBox">
    <div class="hd" id="main/money">
      <ul class="tag flex-bet tc">
        <li class="left-li bbd9">产品列表</li>
        <li class="bbd9">产品续投</li>
      </ul>
    </div>
    <div class="product-wrap bd">
      <ul>
        <ul class="product-list container" v-for="item in product" :key="item.id" @click="goDetail(item.id)">
          <li class="mark" v-if="item.mark">
            <span>{{item.mark|mark}}</span>
            <img src="../../assets/money/mark.png" alt="">
          </li>
          <li class="list-one">{{item.productName}}</li>
          <li class="flex-bet list-two">
            <span>{{item.interestRate}}</span>
            <span>{{item.deadline/30}}</span>
            <span>{{item.investmentAmount}}</span>
          </li>
          <li class="flex-bet list-three">
            <span>预期年化 (%)</span>
            <span>理财期限 (月)</span>
            <span>起投金额 (元)</span>
          </li>
        </ul>
      </ul>
      <ul>
        <ul class="continue-list container" v-for="item in continued" :key="item.id">
          <li class="mark" v-if="item.mark">
            <span>{{item.mark|mark}}</span>
            <img src="../../assets/money/mark.png" alt="">
          </li>
          <li class="list-one">{{item.productName}}</li>
          <li class="list-two">
            <span>{{item.interestRate}}</span>
            <span>{{item.money}}</span>
          </li>
          <li class="list-three">
            <span>预期年化 (%)</span>
            <span>起投金额 (元)</span>
          </li>
          <li class="list-four bte8">{{item.startAt|date}}起息 - {{item.endAt|date}}到期</li>
        </ul>
        <div class="flex-cent continue-box" v-if="isContinue">
          <img src="../../assets/money/continue.png" alt="立即投资">
          <span class="tip">暂无可续投产品，赶紧投资吧</span>
          <a class="btn continue-invest" href="#main/money">立即投资</a>
        </div>
      </ul>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery'
// import TouchSlide from 'touch'
export default{
  data () {
    return {
      product: [],
      continued: [],
      isContinue: false
    }
  },
  created () {
    this.$parent.head.isHead = true // 头部显示
    this.$parent.head.title = '理财列表' // 理财列表
    this.$parent.head.isBell = true // 铃铛隐藏
    this.$parent.head.isWhite = false // 标题不变色
    this.$parent.head.isEnter = false // 标题字体不变
    this.$http(this.path.product).then(res => {
      this.product = res.data.data
      console.log(res.data, this.product)
    })
    this.$http(this.path.continue).then(res => {
      this.continued = res.data.data
      console.log(res.data, this.continued)
      if (res.data.data.length === 0) {
        this.isContinue = !this.isContinue
      }
    })
  },
  mounted () {
    /* eslint-disable */
    // $(function () {
    //   TouchSlide({slideCell: '#leftTabBox'})
    // })
  },
  filters:{
    mark: function (input) {
      switch (input){
        case 1:
          return "新"
        case 2:
          return "热"
      }
    }
  },
  methods: {
    goDetail(id){
      this.$router.push({
        name: `productDetail`,
        query: {
         id:id
        }
      })
    }
  }
}
</script>

<style lang="scss">
.money-view {
  @include center($head-h, $base-h);
  font-family: "PingFang-SC-Medium";
  background: $base-bg;
  .hd {
    .tag {
      position: fixed;
      left: 0;
      right: 0;
      background: #fff;
      z-index: 1;
      li {
        flex-basis: 50%;
        height: $tag-h;
        line-height: $tag-h;
      }
      .left-li::after {
        content: "";
        position: absolute;
        left: 50%;
        height: 0.4rem;
        width: 0.01rem;
        background: linear-gradient(to top, #fff, $border-color, #fff);
      }
    }
    .tag .on {
      color: $title-color;
      border-bottom: 0.02rem solid $title-color;
    }
  }
  .tempWrap {
    margin-top: 0.6rem;
    height: calc(100% - 0.6rem);
  }
  .mark {
    height: 0;
    color: #fff;
    text-align: right;
    font-size: 0.12rem;
    span {
      position: absolute;
      top: 0;
      right: 0.24rem;
    }
  }
  .product-wrap {
    margin-top: 0.6rem;
    height: 100%;
    .product-list {
      position: relative;
      margin-bottom: 0.2rem;
      height: 1.25rem;
      background: #fff;
      .list-one {
        font-size: 0.16rem;
        line-height: 0.5rem;
      }
      .list-two {
        line-height: 0.2rem;
        font-size: 0.15rem;
        & span:first-child {
          font-size: 0.24rem;
          color: $title-color;
        }
        & span:nth-child(1)::after {
          content: "";
          position: absolute;
          left: 33.3%;
          height: 0.35rem;
          width: 0.01rem;
          background: linear-gradient(to top, #fff, $border-color, #fff);
        }
        & span:nth-child(2)::after {
          content: "";
          position: absolute;
          left: 66.66%;
          height: 0.35rem;
          width: 0.01rem;
          background: linear-gradient(to top, #fff, $border-color, #fff);
        }
      }
      .list-three {
        line-height: 0.5rem;
        font-size: 0.13rem;
        color: $tip-color;
      }
    }
    .continue-list {
      position: relative;
      margin-bottom: 0.2rem;
      background: #fff;
      .list-one {
        font-size: 0.16rem;
        line-height: 0.5rem;
      }
      .list-two {
        display: flex;
        line-height: 0.2rem;
        font-size: 0.15rem;
        & span:first-child {
          flex-basis: 40%;
          font-size: 0.24rem;
          color: $title-color;
        }
        & span:nth-child(2) {
          flex-basis: 60%;
        }
        & span:first-child::after {
          content: "";
          position: absolute;
          left: 33.3%;
          height: 0.35rem;
          width: 0.01rem;
          background: linear-gradient(to top, #fff, $border-color, #fff);
        }
      }
      .list-three {
        display: flex;
        line-height: 0.5rem;
        font-size: 0.13rem;
        color: $tip-color;
        & span:first-child {
          flex-basis: 40%;
        }
        & span:nth-child(2) {
          flex-basis: 60%;
        }
      }
      .list-four {
        line-height: 0.25rem;
        color: $list-color;
        font-size: 0.12rem;
        margin: 0 -0.2rem;
        padding: 0 0.2rem;
      }
    }
  }
  .continue-box {
    margin-top: -0.2rem;
    flex-direction: column;
    height: calc(100vh - 2rem);
    span {
      margin-top: 0.1rem;
      line-height: 0.3rem;
    }
    .continue-invest {
      width: 1.6rem;
      line-height: 0.44rem;
      text-align: center;
      color: #fff;
    }
  }
}
</style>



