<template>
  <div class="product-detail-view">
    <div class="top">
      <span class="top-title">{{detail.productName}}</span>
      <i class="fa fa-arrow-left" @click="back()"></i>
      <span class="one">预期年化收益率</span>
      <span class="two">{{detail.interestRate}}</span>
      <ul>
        <li class="flex-bet li-one">
          <span>理财期限（月）</span>
          <span>起投金额（元）</span>
        </li>
        <li class="flex-bet li-two">
          <span>{{detail.deadline/30}}</span>
          <span>{{detail.investmentAmount}}</span>
        </li>
      </ul>
    </div>
    <ul class="main">
      <li class="bbe8">
        <span class="main-title">产品简介</span>
        <router-link :to="{name:'more',params:{msg:detail.moreMessage}}">
          <span class="main-more">查看更多</span>
          <i class="fa fa-chevron-right"></i>
        </router-link>
      </li>
      <li>
        <div>
          <img src="../../assets/money/productDetail/refundStyle.png" alt="">
          <span class="main-left">还款方式</span>
        </div>
        <span class="main-right">{{detail.refundStyle|refundStyle}}</span>
      </li>
      <li>
        <div>
          <img src="../../assets/money/productDetail/startDate.png" alt="">
          <span class="main-left">起息日期</span>
        </div>
        <span class="main-right">{{detail.rateOfInterest|rateOfInterest}}自然日</span>
      </li>
      <li>
        <div>
          <img src="../../assets/money/productDetail/backDate.png" alt="">
          <span class="main-left">到期日期</span>
        </div>
        <span class="main-right">{{end()|date}}</span>
      </li>
      <li>
        <div>
          <img src="../../assets/money/productDetail/productMsg.png" alt="">
          <span class="main-left">产品备注</span>
        </div>
        <span class="main-right">自动还款，3个工作日内到账</span>
      </li>
      <li>
        <div>
          <img src="../../assets/money/productDetail/moreMessage.png" alt="">
          <span class="main-left">产品合同</span>
        </div>
        <div class="main-bottom" @click="checkContract()">
          <span>查看合同</span>
          <i class="fa fa-chevron-right"></i>
        </div>
      </li>
    </ul>
    <div class="buttom-btn flex-bet container">
      <router-link :to="{name:'compute',params:{name:detail.productName,startDate:detail.rateOfInterest}}" ><img src="../../assets/money/productDetail/compute.png" alt="计算器"></router-link>
      <button class="btn">立即投资</button>
    </div>
    <div class="alert flex-cent" v-if="isContract" @click.self="boxHide()">
      <ul class="contract-box">
        <li class="alert-one tc bbe8">查看合同</li>
        <li class="alert-two" :class="{'cantract-click':contractList.isOne}" @click="change('isOne')">
          <i class="fa fa-circle" :class="{'fa-click':contractList.isOne}"></i>
          <span>《出租咨询与服务协议》</span>
        </li>
        <li class="alert-two" :class="{'cantract-click':contractList.isTwo}" @click="change('isTwo')">
          <i class="fa fa-circle" :class="{'fa-click':contractList.isTwo}"></i>
          <span>《授权委托书-出租确认和债...》</span>
        </li>
        <li class="alert-two" :class="{'cantract-click':contractList.isThree}" @click="change('isThree')">
          <i class="fa fa-circle" :class="{'fa-click':contractList.isThree}"></i>
          <span>《授权委托书-催收及诉讼》</span>
        </li>
        <li class="alert-two" :class="{'cantract-click':contractList.isFour}" @click="change('isFour')">
          <i class="fa fa-circle" :class="{'fa-click':contractList.isFour}"></i>
          <span>《出借本金确认书》</span>
        </li>
        <li>
          <button class="alert-three tc" :class="{'btn-disabled':isAbled}" :disabled="isAbled" @click="goContract()">确认</button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default{
  data () {
    return {
      id: '',
      detail: {},
      back: () => { // 点击返回
        this.$router.back(-1)
      },
      isContract: false,
      isAbled: true,
      contractList: {
        isOne: false,
        isTwo: false,
        isThree: false,
        isFour: false
      }
    }
  },
  created () {
    this.getProduct() // 获得产品数据
    this.$http(this.path.productDetail(this.id)).then(res => {
      this.detail = res.data.data
      console.log(res.data.code, this.detail)
    })
  },
  methods: {
    getProduct () {
      this.id = this.$route.query.id
      console.log(this.id)
    },
    end () {
      return (new Date().getTime() + (this.detail.deadline + this.detail.rateOfInterest) * 86400000)
    },
    checkContract () {
      this.isContract = true
    },
    change (value) {
      this.isAbled = false
      for (let i in this.contractList) {
        if (i === value) {
          this.contractList[i] = true
        } else {
          this.contractList[i] = false
        }
      }
    },
    boxHide () {
      this.isContract = !this.isContract
      for (let i in this.contractList) {
        this.contractList[i] = false
        this.isAbled = true
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.contract-box {
  width: 3rem;
  border-radius: 0.1rem;
  background: #fff;
  .alert-one {
    margin-bottom: 0.2rem;
    line-height: 0.5rem;
    font-size: 0.17rem;
  }
  .alert-two {
    padding-left: 0.5rem;
    color: #666;
    line-height: 0.3rem;
  }
  .alert-three {
    margin-top: 0.2rem;
    width: 100%;
    color: #fff;
    line-height: 0.44rem;
    background: $btn-bg;
    font-size: 0.17rem;
    border-radius: 0 0 0.1rem 0.1rem;
  }
  .fa-circle {
    margin-right: 0.1rem;
    font-size: 0.12rem;
    color: #c0c0c0;
  }
  .cantract-click {
    color: $title-color;
    text-decoration: underline;
  }
  .fa-click {
    color: $title-color;
  }
  .btn-disabled{
    color: #666;
  }
}
.product-detail-view {
  @include center(0, 0);
  font-family: "PingFang-SC-Medium";
  background: $base-bg;
  .top {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 0.2rem;
    color: #fff;
    background: linear-gradient(#ffb12e, #d6a956);
    .top-title {
      margin-top: 0.15rem;
      font-size: $head-f;
    }
    .fa-arrow-left {
      position: absolute;
      left: 0.2rem;
      top: 0.2rem;
      -webkit-text-stroke: 0.01rem #d6a956;
      font-size: $enter-f;
    }
    .one {
      font-size: 0.13rem;
      line-height: 0.4rem;
    }
    .two {
      font-size: 0.5rem;
      font-family: HelveticaNeue-Thin;
      line-height: 1rem;
      &::after {
        content: "%";
        font-size: 0.2rem;
      }
    }
    ul {
      width: 100%;
      .li-one {
        color: #f9e1b9;
        font-size: 0.12rem;
      }
      .li-two {
        font-size: 0.17rem;
      }
      li {
        line-height: 0.3rem;
        span {
          flex-basis: 50%;
          text-align: center;
        }
      }
    }
  }
  .main {
    margin-top: 0.1rem;
    margin-bottom: 1rem;
    background: #fff;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 0.2rem;
      line-height: 0.4rem;
      img {
        margin-right: 0.1rem;
      }
      .fa-chevron-right {
        color: $list-color;
        font-size: 0.15rem;
        vertical-align: middle;
        -webkit-text-stroke: 0.01rem #fff;
      }
      .main-more {
        font-size: 0.12rem;
      }
      .main-left {
        color: $list-color;
      }
      .main-right {
        color: $tip-color;
      }
      .main-bottom {
        color: #dfa584;
        .fa-chevron-right {
          color: #dfa584;
        }
      }
    }
  }
  .buttom-btn {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    padding-bottom: 0.2rem;
    background: $base-bg;
    .btn {
      width: 2.8rem;
      color: #fff;
    }
  }
}
</style>



